import React from 'react';
import { Card, Row, Col, Badge } from 'react-bootstrap';
import { ResourceManager } from '../utils/resourceManager';

const RessourcenUebersicht = ({ resources, brauvorgaenge }) => {
  const resourceManager = new ResourceManager(resources, brauvorgaenge);
  const status = resourceManager.getRessourcenStatus();

  const verfuegbareGaertanks = status.gaertanks.filter(t => t.status === 'verfgbar').length;
  const verfuegbareFaesser = status.faesser.filter(f => f.status === 'verfgbar').length;

  return (
    <Card className="mb-4">
      <Card.Header>
        <h5>Ressourcenbersicht</h5>
      </Card.Header>
      <Card.Body>
        <Row>
          <Col md={6}>
            <h6>Grtanks ({verfuegbareGaertanks}/{status.gaertanks.length} verfgbar)</h6>
            {status.gaertanks.map(tank => (
              <div key={tank.id} className="mb-2">
                <Badge bg={tank.status === 'verfgbar' ? 'success' : 'danger'}>
                  {tank.name}
                </Badge>
                <span className="ms-2 text-muted">{tank.status}</span>
              </div>
            ))}
          </Col>
          <Col md={6}>
            <h6>Fsser ({verfuegbareFaesser}/{status.faesser.length} verfgbar)</h6>
            <div className="mb-2">
              <Badge bg="success">Verfgbar: {verfuegbareFaesser}</Badge>
            </div>
            <div className="mb-2">
              <Badge bg="danger">Belegt: {status.faesser.length - verfuegbareFaesser}</Badge>
            </div>
            <small className="text-muted">
              Fr einen Brauvorgang werden 3 Fsser bentigt
            </small>
          </Col>
        </Row>
      </Card.Body>
    </Card>
  );
};

export default RessourcenUebersicht;
