import React, { useState, useEffect } from 'react';
import { Modal, Form, Button, Alert } from 'react-bootstrap';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { ResourceManager } from '../utils/resourceManager';
import { brauzeiten } from '../data/resources';

const BrauvorgangModal = ({ show, handleClose, resources, brauvorgaenge, onSave }) => {
  const [titel, setTitel] = useState('');
  const [startDatum, setStartDatum] = useState(new Date());
  const [brauart, setBrauart] = useState('obergrig');
  const [fehlermeldung, setFehlermeldung] = useState('');
  const [resourceManager] = useState(() => new ResourceManager(resources, brauvorgaenge));

  useEffect(() => {
    if (show) {
      setTitel('');
      setStartDatum(new Date());
      setBrauart('obergrig');
      setFehlermeldung('');
    }
  }, [show]);

  const handleSave = () => {
    setFehlermeldung('');

    if (!titel.trim()) {
      setFehlermeldung('Bitte geben Sie einen Titel ein');
      return;
    }

    const gaertankVerfuegbar = resourceManager.pruefeGaertankVerfuegbarkeit(startDatum, brauart);
    const faesserVerfuegbar = resourceManager.pruefeFaesserVerfuegbarkeit(startDatum, brauart);

    if (!gaertankVerfuegbar && !faesserVerfuegbar) {
      setFehlermeldung('Kein Grtank frei und nicht genug Fsser verfgbar');
      return;
    }

    if (!gaertankVerfuegbar) {
      setFehlermeldung('Kein Grtank frei');
      return;
    }

    if (!faesserVerfuegbar) {
      setFehlermeldung('Nicht genug Fsser verfgbar');
      return;
    }

    const gaertank = resourceManager.getVerfuegbarenGaertank(startDatum, brauart);
    const faesser = resourceManager.getVerfuegbareFaesser(startDatum, brauart);

    const brauvorgang = {
      id: Date.now(),
      titel,
      startDatum,
      brauart,
      gaertankId: gaertank.id,
      gaertankName: gaertank.name,
      belegteFaesser: faesser.map(f => f.id),
      faesserNamen: faesser.map(f => f.name),
      typ: 'brauvorgang'
    };

    onSave(brauvorgang);
    handleClose();
  };

  return (
    <Modal show={show} onHide={handleClose} size="lg">
      <Modal.Header closeButton>
        <Modal.Title>Neuer Brauvorgang</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {fehlermeldung && <Alert variant="danger">{fehlermeldung}</Alert>}
        
        <Form>
          <Form.Group className="mb-3">
            <Form.Label>Titel</Form.Label>
            <Form.Control
              type="text"
              value={titel}
              onChange={(e) => setTitel(e.target.value)}
              placeholder="Titel des Brauvorgangs eingeben"
              required
            />
          </Form.Group>

          <Form.Group className="mb-3">
            <Form.Label>Startdatum</Form.Label>
            <DatePicker
              selected={startDatum}
              onChange={setStartDatum}
              className="form-control"
              dateFormat="dd.MM.yyyy"
              minDate={new Date()}
            />
          </Form.Group>

          <Form.Group className="mb-3">
            <Form.Label>Brauart</Form.Label>
            <Form.Select
              value={brauart}
              onChange={(e) => setBrauart(e.target.value)}
            >
              <option value="obergrig">Obergrig (14 Tage)</option>
              <option value="untergrig">Untergrig (10 Tage)</option>
            </Form.Select>
          </Form.Group>

          <div className="mb-3">
            <h6>Bentigte Ressourcen:</h6>
            <ul>
              <li>1 Grtank fr {brauzeiten[brauart].tage} Tage</li>
              <li>3 Fsser fr {brauzeiten[brauart].tage} Tage</li>
            </ul>
          </div>

          <div className="resource-status">
            <h6>Verfgbarkeitsprfung:</h6>
            {resourceManager.pruefeGaertankVerfuegbarkeit(startDatum, brauart) ? (
              <div className="resource-available">
                 Grtank verfgbar
              </div>
            ) : (
              <div className="resource-unavailable">
                 Kein Grtank verfgbar
              </div>
            )}
            {resourceManager.pruefeFaesserVerfuegbarkeit(startDatum, brauart) ? (
              <div className="resource-available">
                 Genug Fsser verfgbar
              </div>
            ) : (
              <div className="resource-unavailable">
                 Nicht genug Fsser verfgbar
              </div>
            )}
          </div>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Abbrechen
        </Button>
        <Button 
          variant="success" 
          onClick={handleSave}
          disabled={!titel.trim() || 
                   !resourceManager.pruefeGaertankVerfuegbarkeit(startDatum, brauart) || 
                   !resourceManager.pruefeFaesserVerfuegbarkeit(startDatum, brauart)}
        >
          Brauvorgang speichern
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default BrauvorgangModal;
